import React, { Fragment } from "react";
import NavBar from "./../../layouts/common/NavBar/NavBar";
import Breadcrumb from "./../../layouts/common/Breadcrumb/Breadcrumb";
import BannerTitle from "./../../layouts/common/BannerTitle/BannerTitle";
import { Link } from "react-router-dom";
import MapaSite from "./../../layouts/common/MapaSite/MapaSite";
import RedesSociais from "./../../layouts/common/RedesSociais/RedesSociais";
import Footer from "./../../layouts/common/Footer/Footer";
import PageComponent from "../../core/PageComponent";
import FormContato from './../../forms/common/FormContato';

export default class Contact extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configuro landscape --> */}
        <div id="lying" />

        {/* <!-- Configuro natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside marketing-conteudo">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Contato</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb>
                  <li>
                    <Link to="/">dgitozero</Link>
                  </li>
                  <li>
                    <span>Contato</span>
                  </li>
                </Breadcrumb>

                <div className="section">
                  <div className="container">
                    <div className="row">
                      <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12 text-center">
                        <p>
                            Este formulrio  destinado  solicitao de
                            oramentos, dvidas gerais sobre o funcionamento do
                            site e/ou assuntos relacionados  empresa.
                          </p>
                        <br className="clear" />
                        <div className="row">
                          <FormContato />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



