import React, { Component, Fragment } from 'react';
import { Link } from 'react-router-dom';
import DesignSVG from './../../../SVG/DesignSVG';
import InboundSVG from './../../../SVG/InboundSVG';
import AdsSVG from './../../../SVG/AdsSVG';


class Solucoes extends Component {
    render() {
        return (
          <Fragment>
             {/* Design */}
              <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12">
                <div className="wrapper-content border">
                  <div className="icon design">
                    <DesignSVG />
                  </div>
                  <h3>
                    <Link to="/design">Design</Link>
                  </h3>
                  <ul className="oppening-list">
                    <li>
                      <Link to="/design/branding">Branding</Link>
                    </li>
                    <li>
                      <Link to="/design/ux">UX</Link>
                    </li>
                  </ul>
                  <button className="button">Solues Design</button>
                </div>
              </div>
              {/* Fim Design */}

              {/* Inbound */}
              <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12">
                <div className="wrapper-content border">
                  <div className="icon inbound">
                    <InboundSVG />
                  </div>
                  <h3>
                    <Link to="/inbound-marketing">Inbound</Link>
                  </h3>
                  <ul className="oppening-list">
                    <li>
                      <Link to="/inbound-marketing/seo">SEO</Link>
                    </li>
                    <li>
                      <Link to="/inbound-marketing/content-marketing">Marketing de Contedo</Link>
                    </li>
                    <li>
                      <Link to="/inbound-marketing/corporate-blog">Blog Corporativo</Link>
                    </li>
                    <li>
                      <Link to="/inbound-marketing/email-marketing">E-mail Marketing</Link>
                    </li>
                    <li>
                      <Link to="/inbound-marketing/google-analytics">Google Analytics</Link>
                    </li>
                  </ul>
                  <button className="button">Solues SEO</button>
                </div>
              </div>
              {/* Fim Inbound */}

              {/* Publicidade Online */}
              <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12">
                <div className="wrapper-content border">
                  <div className="icon ads">
                    <AdsSVG />
                  </div>
                  <h3>
                    <Link to="/online-advertising">Publicidade Online</Link>
                  </h3>
                  <ul>
                    <li>
                      <Link to="/online-advertising/google-adwords">Google Adwords</Link>
                    </li>
                    <li>
                      <Link to="/online-advertising/sponsored-links">Links Patrocinados</Link>
                    </li>
                    <li>
                      <Link to="/online-advertising/facebook-ads">Facebook Ads</Link>
                    </li>
                    {/* <li>
                      <Link to="/online-advertising/youtube-ads">Youtube Ads</Link>
                    </li> */}
                    <li>
                      <Link to="/online-advertising/linkedin-ads">LinkedIn Ads</Link>
                    </li>
                    <li>
                      <Link to="/online-advertising/remarketing">Remarketing</Link>
                    </li>
                  </ul>
                  <button className="button">Solues Ads</button>
                </div>
              </div>

              {/* Fim Publicidade Online */}
              </Fragment>
        );
    }
}

export default Solucoes;


